<script>
import $ from "jquery";
import { Printd } from "printd";
import Qeditor from "@/components/Quill";
import IconSelect from "@/components/IconSelect";
import FileRecord from "@/components/FileRecord";
import PhoneSurface from "@/components/PhoneSurface";
import docCss from "./docCss";
import printCss from "./printCss";
import RealPhone from './RealPhonePreview'
import html2canvas from "html2canvas";
import canvg from "canvg";
import JSZip from "jszip";

// 函数防抖，防止用户多次点击按钮
function debounce(fn, t) {
  // 传一个回调函数
  let Timer = null;
  return function () {
    clearTimeout(Timer); // 清除定时器
    Timer = setTimeout(() => {
      fn.apply(this, arguments);
    }, t);
  };
}
let zip = new JSZip();

function canvasToBlack(canvas) {
    const ctx = canvas.getContext('2d')
    var imageData  = ctx.getImageData(0,0,canvas.width, canvas.height);
    var data = imageData.data;

    for (var i = 0; i < data.length; i += 4) {
        if(data[i]<5&&data[i+1]<5&&data[i+2]<5){
            data[i] = data[i + 1] =data[i + 2] = 255;
        }else{
            data[i] = data[i + 1] =data[i + 2] = 0;
        }
    }
    ctx.putImageData(imageData,0,0,0,0,canvas.width, canvas.height);
}

function toCanvas(el,black) {
  const svgElems = el.querySelectorAll("svg");
  const imgElems = el.querySelectorAll("img");

  let elems = [...svgElems, ...imgElems];
  elems.forEach((node) => {
    let parentNode = node.parentNode;
    let canvas = document.createElement("canvas");
    canvas.style.zIndex = 9;
    //处理svg转换canvas需要使用canvg组件
    if (node.tagName == "svg") {
      let svg = node.outerHTML.trim();
      canvg(canvas, svg);
      if (node.style.position) {
        canvas.style.position += node.style.position;
        canvas.style.left += node.style.left;
        canvas.style.top += node.style.top;
      }
    }
    //处理img转换canvas
    if (node.tagName == "IMG") {
      canvas.width = node.width;
      canvas.height = node.height;
      //5个参数都加,不然firefox会有问题!!!!
      canvas.getContext("2d").drawImage(node, 0, 0,node.width,node.height);
    }
    parentNode.removeChild(node);
    parentNode.appendChild(canvas);
    if(black) canvasToBlack(canvas)//canvas渲染之后才能重绘
  });
}

//像素透明化处理
function whiteToTransparent(ctx, img) {
  // ctx.drawImage(img, 0, 0);
  var imageData = ctx.getImageData(0, 0, img.width, img.height);
  var data = imageData.data;
  for (var i = 0; i < data.length; i += 4) {
    var r = data[i];
    var g = data[i + 1];
    var b = data[i + 2];

    // 设定阈值，根据需要调整
    var threshold = 250;
    if (r > threshold && g > threshold && b > threshold) {
      data[i + 3] = 0; // 透明度设置为0
    }
  }
  ctx.putImageData(imageData, 0, 0);
}



