    //   }, []);
    //   const coverBox = document.querySelector('.ghp2.lens .box').outerHTML
    //   console.log('coverBox :>> ', coverBox);

    //   return `
    //     <div id='numBox-lens' class="${boxClass}">
    //       ${pages
    //         .map((nums) => {
    //           if(+this.rn_label === 1) {
    //             // 覆盖在卡片上的数字，为了确定房间号位置
