    // },
    getStick(v) {
      /* 1-->（无）rn控件+（有）房间号,批量只生成一页 */

      const stick = `<div class="wrap ">${this.$refs.print.outerHTML}</div>`;
      const limit =
        this.stick_type === "unicom"
          ? v === "A4"
            ? 9
            : 20
          : v === "A4"
            ? 12
            : 24;
      const note = `
        <div class="page-note">
            <span>Note: The dotted line is the cutting line</span>
            <span>${v}</span>
            <span>Scale:1:1</span>
        </div>
      `;

      if (this.rn && !$("#qeditor #rn").length) {
        let page = `<div class="paper">
        <div class="row2">
            <div class="row2-left"></div>
            <div class="page"><div class="page-box">
                ${stick.repeat(limit)}
                </div></div>
            <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
                    </div>`;
        return `<div id="numBox-stick">${page}</div>`;
      }
      /* 先验证 [] 中格式（数字或空格，且必须存在数字），不满足当做普通文本处理 */
      let target = "";
      const span = document.querySelectorAll("#qeditor #rn span");

      if (!span.length) return ''

      span.forEach(s => {
        if (/.*\[$/.test(s.innerText.trim())) {
          target += s.innerText
        }
        if (/^\].*/.test(s.innerText.trim())) {
          target += s.innerText
        }
        if (s.style.backgroundColor === 'rgb(255, 255, 255)') {
          target += s.innerText
        }
      });

      if (!/.*\[[\s\d]*\d+[\s\d]*\]/.test(target)) return ""

      target = target.replace(/[^\d]/g, ""); //提取其中的数字进行替换

      /* 2-->（有）rn控件+（无）房间号,批量只生成一页+rn空白格 */
      if ($(".ghp2 #rn").length && !this.rn) {
        let page = `<div class="paper">
        <div class="row2">
          <div class="row2-left"></div>
          <div class="page"><div class="page-box">
               ${stick +
          stick
            .replace(
              eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
              "$1          $2"
            )
            .repeat(limit - 1)
          }
               </div></div>
          <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
                    </div>`;
        return `<div id="numBox-stick">${page}</div>`;
      }


      /* 4-->（有）rn控件+（有）房间号,批量只生成一页 */
      let pages = [];
      let str = "";
      const RNS = this.getRN(380); //20张限制
      RNS.forEach((v, i) => {
        if (i && (i + 1) % limit === 0) {
          str += stick.replace(
            eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
            "$1  " + v + "  $2"
          );
          let page = `<div class="paper">
            <div class="row2">
                <div class="row2-left"></div>
                <div class="page"><div class="page-box">${str}</div></div>
                <div class="row2-right">${note}</div>
            </div>
            <div class="row3">${note}</div>
                        </div>`;
          pages.push(page);
          str = "";
        } else {
          str += stick.replace(
            eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
            "$1  " + v + "  $2"
          );
        }
      });
      //   不凑满一页的情况补全
      let add = 0;
      if (RNS.length >= 1 && RNS.length % limit) {
        add = (parseInt(RNS.length / limit) + 1) * limit - RNS.length;
        let page = `<div class="paper">
        <div class="row2">
            <div class="row2-left"></div>
            <div class="page"><div class="page-box">${(str += stick
            .replace(
              eval("/(.*<span.*)" + target + "(.*span>.*)/g"),
              "$1          $2"
            )
            .repeat(add))}</div></div>
            <div class="row2-right">${note}</div>
        </div>
        <div class="row3">${note}</div>
        </div>`;
        pages.push(page);
      }
      return `<div id="numBox-stick">${pages.join("")}</div>`;
    },

    validate() {
      const { quantity, contact, phone, email } = this.table;
      const errors = [];
      !quantity && errors.push("quantity");
      !contact && errors.push("contact");
      !phone && errors.push("phone");
      !email && errors.push("email");
      //   表格必填项验证
      if (errors.length) {
        // this.$emit("load", false);
        this.$message.closeAll();
        this.$message.error(this.$t("INPUT_REQUIRED"));
        return false;
      }
      return true;
    },

    paperOK() {
      this.paperDialog = false;
      this.getPdf();
    },

    getPdf() {
      const style = "./iconfonts/iconStyle.css?" + new Date().getTime();
      const style2 = "./iconfonts/quill.css?" + new Date().getTime();
      const scripts = []; //通过launchPrint延长时间，加载资源

      const section = document.createElement("section");
      let customRN = "";
      const { custom, isGHP61, paper } = this;
      if (!isGHP61 && custom === "lens") {
        try {
          customRN = this.getLens();
        } catch (err) {
          //   this.$emit("load", false);
          this.$message.closeAll();
          return this.$message.error(this.$t("ROOM_NUM_ERR"));
        }
        section.innerHTML = this.getDoc() + customRN;
        const printCallback = ({ launchPrint }) =>
          setTimeout(() => {
            launchPrint();
          }, 500);
        const d = new Printd();
        const iframe = d.getIFrame();
        const filename = isGHP61
          ? "GHP61x_silk_screen"
          : custom === "lens"
            ? "GHP62x_silk_screen"
            : "GHP62x_cardstock";

        iframe.addEventListener("load", () => {
          const { contentWindow } = iframe;
          contentWindow.document.title = filename; //实现Firefox打印导出的文件默认名=title
          document.title = filename; // other browers
        });

        d.print(
          section,
          [
            style,
            style2,
            printCss,
            docCss,
            `
                    @page {
                    size: A4 landscape;
                    margin: 5mm;
                    }`,
          ],
          scripts,
          printCallback
        );
      } else {
        try {
          customRN = isGHP61 ? '' : this.getStick(paper);
        } catch (err) {
          //   this.$emit("load", false);
          this.$message.closeAll();
          return this.$message.error(this.$t("ROOM_NUM_ERR"));
        }

        section.innerHTML = this.getDoc() + customRN;
        const printCallback = ({ launchPrint }) =>
          setTimeout(() => {
            launchPrint();
          }, 500);
        const d = new Printd();
        const iframe = d.getIFrame();
        const filename = isGHP61
          ? "GHP61x_silk_screen"
          : custom === "lens"
            ? "GHP62x_silk_screen"
            : "GHP62x_cardstock";

        const { contentWindow } = iframe;
        iframe.addEventListener("load", () => {
          contentWindow.document.title = filename; //实现Firefox打印导出的文件默认名=title
          document.title = filename; // other browers
        });
        if (!isGHP61) {
          section.classList.add(paper);
          section.classList.add(this.stick_type);
        }

        d.print(
          section,
          [
            style,
            style2,
            printCss,
            docCss,
            `
                @page {
                size: ${isGHP61 || paper === "A4"
              ? "A4 landscape"
              : `A3 ${this.stick_type === "unicom" ? "portrait" : "landscape"
              }`
            };
                margin:5mm;margin-bottom: 0mm ;
                ${paper === "A3" && this.stick_type === "cutoff"
              ? "margin-right: 0mm ;"
              : isGHP61 ? "margin:5mm" : ''
            }
                }
                `,
          ],
          scripts,
          printCallback
        );
      }
    },
    // 打印pdf
    print: debounce(
      function () {
        if (!this.validate()) {
          return false;
        }

        this.paper = "A4";
        if (!this.isGHP61 && this.custom === "lens") {
          if (!this.color && !this.table.pms) {
            //   this.$emit("load", false);
            this.$message.closeAll();
            return this.$message.error(this.$t("LOGO_PMS"));
          }
          this.$confirm(
            this.$t("CONFIRM_LOGO_COLOR", { 0: this.table.pms }),
            this.$t("TIP"),
            {
              center: true,
              showClose: false,
              confirmButtonText: this.$t("CONFIRM"),
              cancelButtonText: this.$t("CLOSE"),
              dangerouslyUseHTMLString: true,
            }
          ).then(() => {
            this.paperDialog = true;
          });
        } else {
          if (this.custom === 'stick') {
            this.paper = 'A3'
          }
          this.paperDialog = true;
        }
      },
      500,
      true
    ),
    updateQuantity() {
      let n = 0
      if (!this.isGHP61 && this.custom === "lens") {
        n =this.getRN(29106); //一百张限制
      }else{
        n = this.getRN(380); //20张限制
      }
      this.table.quantity = n.length || ''
    },
    base64ToFile(dataurl) {
      let arr = dataurl.split(",");
      let mime = arr[0].match(/:(.*?);/)[1];
      let bstr = atob(arr[1]);
      let n = bstr.length;
      let u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], {
        type: mime,
      });
    },
    //镂空图片，只留要丝印的部分
    toHollow(parentNode,black) {
      const rnb = parentNode.querySelector('#rn-box')
      rnb.setAttribute("style", "background-color: rgb(0, 0, 0); height: 33px; border-radius: 4px;");
      rnb.style.color = '#000'
      if(black) {
        $(parentNode).addClass("black")
        const span = parentNode.querySelectorAll('span')
        span.forEach(el => el.style.color = "#000");
      }
    },
    restorePreview() {
      location.reload()
      const preview = $("#print-preview.black")[0];
      $(preview).removeClass("black")
      $(preview).removeClass('hollow')
      this.loading = false
    },
    addImgToZip(canvas, black, fname) {
      const wrapper = $('#canvas1')[0]
      const ctx = wrapper.getContext("2d");
      const wSplit = this.isFirefox ? 14 :18
      ctx.drawImage(canvas, wSplit, 56, 448, 516, 0, 0, 448, 516)
      whiteToTransparent(ctx, canvas)
      let blob = this.base64ToFile(ctx.canvas.toDataURL("image/png"));
      zip.file(fname, blob, { Blob: true });
    },
    getZip(black = 1) {
      this.saveHtml()
      this.laserDialog = false
      $('.el-scrollbar__wrap')[0].scrollTop = 0
      zip = new JSZip();
      this.loading = true
      const preview = $("#print-preview")[0];
      const rn = $("#print-preview #rn")[0];
      const fSize = parseInt($(rn).find(':first')[0].style.fontSize)+4+'px' || '24px'
      const target = $(`<span id="rn-box" style="${black ? 'color:#000;' : 'color:#aaabae;'} font-size: ${fSize}; font-family: Arial;"></span>`)[0]
      $(preview).addClass('hollow')
      this.toHollow(preview,black)
      const pname = 'GHP62X_LaserEngraving';

      toCanvas(preview,black)
      const canvas_params = {
        taintTest: true, // 检测每张图片都已经加载完成
        background: "transparent",
      }
      try {
        if (this.rn) {//多张图（带房间号，限制30张图
          const nums = this.getRN(30)
          setTimeout(() => {
            nums.forEach((v, i) => {
              const rnBox = $('#print-preview #rn-box')[0]
              target.innerText = v
              rn.replaceChild(target, rnBox)
              html2canvas(preview, canvas_params).then((canvas) => {
                this.addImgToZip(canvas, black, `${pname + '_' + v}.png`)
                if (i === nums.length - 1) this.downloadZip()
              })
            });
          }, 500);//这里定时器是为等待loading渲染之后，再进行dom操作生产图片

        } else {//一张图
          html2canvas(preview, canvas_params)
            .then((canvas) => {
              this.addImgToZip(canvas, black, `${pname}.png`)
              this.downloadZip()
            })
        }
      } catch (error) {
        //catch 房间号错误和html2canvas可能抛出的转化错误
        sessionStorage.setItem('clear',1)
        this.restorePreview()
      }

    },
    downloadZip() {
      zip.generateAsync({ type: "blob" }).then((content) => {

        const link = $('#img-link')[0]
        link.href = URL.createObjectURL(content);
        link.click();
        sessionStorage.setItem('clear',1)
        this.restorePreview()
      });
    },
  },
  watch: {
    locale() {
      setTimeout(() => {
        this.setSaveVal()
      }, 0);
    },
    isGHP61() {
      setTimeout(() => {
        this.setSaveVal()
      }, 0);
    },
    custom() {
      setTimeout(() => {
        this.setSaveVal()
      }, 0);
    },
  }
};
</script>

