import Vue from "vue";
import App from "./App.vue";
import { i18n } from "./init";

// 解决同源下的多窗口操作数据库会失败
window.addEventListener('storage', () => {
  if(window.db){
    window.db.close()//打开新tab的时候，其他同源tab的有DB的关闭数据库
  }
}, true)


Vue.config.productionTip = false;

new Vue({
  i18n,
  render: (h) => h(App),
}).$mount("#app");
