<template>
  <div id="app" v-loading="loading">
    <el-scrollbar class="scrollbar">
      <XML id="xml" :load="loading" @load="getLoading" />
    </el-scrollbar>
    <div id="video">
      <img width="48px" height="48px" src="~@/assets/video.svg" alt="" />
      <div>{{ $t("TUTOR_VIDEO") }}</div>
    </div>
  </div>
</template>

<script>
import XML from "./components/XML.vue";
import { i18n } from "./init";

window.addEventListener("DOMContentLoaded", () => {
  let beforeX, beforeY, afterX, afterY,locale;
  const draggable = document.getElementById("video");
  let initialMouseY;
  let initialDivY;
  function dragDiv(event) {
    const currentMouseY = event.clientY;
    const offset = currentMouseY - initialMouseY;
    const newY = initialDivY + offset;
    // 防止div超出页面顶部和底部
    const maxY = window.innerHeight - draggable.offsetHeight;
    const minY = 0;
    const newDivY = Math.max(minY, Math.min(newY, maxY));
    draggable.style.top = newDivY + "px";
  }
  // 元素垂直拖拽功能
  function startDrag(e) {
    locale = i18n.locale;
    [ beforeX, beforeY ] = [ e.offsetX, e.offsetY ];
    initialMouseY = e.clientY;
    initialDivY = draggable.offsetTop;
    document.addEventListener("mousemove", dragDiv);
    document.addEventListener("mouseup", stopDrag);
  }
  function stopDrag() {
    document.removeEventListener("mousemove", dragDiv);
    document.removeEventListener("mouseup", stopDrag);
  }
  function upDrag(e) {
    [ afterX, afterY ] = [ e.offsetX, e.offsetY ];
  }
  function click() {
    // 对比鼠标按下和松开的位置，来区分到底是拖拽还是点击
    if (beforeX === afterX && beforeY === afterY) {
      if (locale === "zh") {
        window.open(
          "https://v.youku.com/v_show/id_XNTk4MDE4MjU1Ng==.html?spm=a2hcb.playlist.page.15&playMode=pugv",
          "_blank"
        );
      } else {
        window.open(
          "https://youtu.be/JZUcdIMzlL4?si=_LTgVjQqWNZl6uwj",
          "_blank"
        );
      }
    }

  }
  draggable.addEventListener("click", click);
  draggable.addEventListener("mousedown", startDrag);
  draggable.addEventListener("mouseup", upDrag);

});

export default {
  name: "App",
  components: {
    XML,
  },
  data() {
    return {
      loading: false,
    };
  },
  methods: {
    getLoading(v) {
      this.loading = v;
    },
  },
};
</script>

