<template>
  <div class='real-phone-preview' v-if='visible'>
    <div class='preview-mask'>
    <div class="preview-wrapper">
    <div class='preview-main'>
      <span class="preview-close" @click="$emit('cancel')"></span>
      <Phone size='big' :color="color" :content="content" :mode='mode' :surface="surface"/>
      <ul class='preview-type-switch'>
        <li 
          @click="switchType('white')" 
          :class="color === 'white' ? 'active' : ''"
        >
          <Phone size='small' color='white' :content="content" :mode='mode' :surface="surface"/>
        </li>
        <li 
          @click="switchType('black')" 
          :class="color === 'black' ? 'active' : ''"
        >
          <Phone size='small' color='black' :content="content" :mode='mode' :surface="surface" />
        </li>
      </ul>
    </div>
    </div>
    </div>
  </div>
</template>
<script>
import Phone from './Phone'
export default {
  props: {
    visible: Boolean,
    mode: String,
    surface:String
  },
  
  components: {
    Phone
  },
  data() {
    return {
      color: 'white',
      content: ''
    }
  },
  methods: {
    switchType(color){
      this.color = color
    },
  },
  watch: {
    visible(v) {
      if(v) {
        this.content = document.getElementById('print-preview').outerHTML
      }
    }
  }

}
</script>
