<template>
  <div>
    <div class="file-btns">
      <el-button type="primary" @click="exportFile"
        >{{ $t("EXPORT") }} <i class="el-icon-download"
      /></el-button>
      <br /><br />
      <div class="import-file">
        <el-upload
          ref="upload"
          action="#"
          :auto-upload="false"
          accept=".txt"
          :on-change="importFile"
          :on-exceed="exceedFile"
          :limit="1"
          :show-file-list="false"
        >
          <el-button type="primary"
            >{{ $t("IMPORT") }} <i class="el-icon-upload2"
          /></el-button>
        </el-upload>
      </div>
      <div class="record" @click="openRecord">{{ $t("FILE_RECORD") }}</div>
    </div>
    <el-drawer
      :title="$t('FILE_RECORD')"
      :visible.sync="fileRecordVis"
      :modal-append-to-body="false"
      direction="ltr"
      class="file-record-drawer"
      size="65%"
    >
      <div class="record-wrapper">
        <table>
          <thead>
            <tr>
              <th>#</th>
              <th>{{ $t("FILE_FROM") }}</th>
              <th>{{ $t("FILE_DATE") }}</th>
              <th>{{ $t("PROJECT_NAME") }}</th>
              <th>{{ $t("CUSTOM_TYPE") }}</th>
              <th>{{ $t("FILE") }}</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in objStore" :key="item.time">
              <td>{{ index + 1 }}</td>
              <td>{{ $t(item.action) }}</td>
              <td>{{ formatDate(item.time) }}</td>
              <td>{{ item.name }}</td>
              <td>{{ $t(item.type) }}</td>
              <td>{{ item.content.name }}</td>
              <td>
                <img
                  :title="$t('DOWNLOAD')"
                  width="24"
                  height="24"
                  src="~@/assets/download.svg"
                  alt=""
                  @click="downloadFile(item.content)"
                />&nbsp;
                <img
                  :title="$t('APPLY')"
                  width="24"
                  height="24"
                  src="~@/assets/apply.svg"
                  alt=""
                  @click="applyFile(item.content, item.type)"
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </el-drawer>
    <el-dialog
      :title="$t('TIP')"
      :visible.sync="applyConfirmVis"
      width="30%"
      :modal-append-to-body="false"
      center
      class="apply-dialog"
      :show-close="false"
    >
    <span>{{ $t("FILE_APPLY_CONTINUE_TIP") }}</span>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="applyConfirmVis = false">{{ $t('CANCEL') }}</el-button>
      <el-button size="small" type="primary" @click="writeFile(itemFile)">{{ $t('SURE')}}</el-button>
    </div>
    </el-dialog>
  </div>
</template>

<script>
// 函数防抖，防止用户多次点击按钮
function debounce(fn, t) {
  // 传一个回调函数
  let Timer = null;
  return function () {
    clearTimeout(Timer); // 清除定时器
    Timer = setTimeout(() => {
      fn.apply(this, arguments);
    }, t);
  };
}

window.onload = function () {
  localStorage.setItem('openDB',new Date().getTime())
}
export default {
  name: "file-record",
  props: ["table", "custom"],
  data() {
    return {
      db: null,
      objStore: [],
      fileRecordVis: false,
      applyConfirmVis:false,
      itemFile:'',// 当前准备应用的file,
    };
  },
  created() {
    this.initDB().then(() => {
      this.getDB()
    })
  },
  destroyed() {
    this.db.close();
  },
  computed: {
    formatDate() {
      return (v) => {
        const date = new Date(v);
        const time = (date + "").match(/\d{2}:\d{2}:\d{2}/)[0];
        const yy = date.getFullYear();
        const mm = date.getMonth() + 1;
        const dd = date.getDate();
        return yy + "-" + mm + "-" + dd + " " + time;
      };
    },
  },
  methods: {
    openRecord() {
      this.getDB().then(() => {
        this.fileRecordVis = true;
      }).catch(() => {
        // 场景：多个页面如果跳回前一个页面，其数据库已经关闭，需要重载页面以关闭其他同源tab的数据库，重新开启当前tab的数据库
        location.reload()
      })
    },
    // 初始化数据库
    initDB() {
      if (this.db) this.db.close();

      return new Promise((reslove, reject) => {
        const indexedDB = window.indexedDB || window.webkitIndexedDB || window.mozIndexedDB;
        try {
          //如果指定的数据库不存在，就会新建数据库
          const request = indexedDB.open("gsDB", new Date().getTime());

          // success 事件表示成功打开数据库
          request.onsuccess = (event) => {
            this.db = event.target.result;
            // 将当前数据库对象挂在window上
            window.db = event.target.result;
            console.log("open DB success");
            reslove()
          };
          request.onerror = (e) => {
            console.log("open DB error", e);
            reject()
          };
          // IndexedDB API 中不允许数据库中的数据仓库在同一版本中发生变化，
          // 所以需要在 indexedDB.open 方法中传入新的版本号来更新版本，避免在同一版本中重复修改数据库。

          request.onupgradeneeded = function (e) {
            //数据库升级事件,创建表一定要在此事件中执行
            this.db = e.target.result;
            console.log("updated DB version is：" + e.newVersion);
            if (!this.db.objectStoreNames.contains("ghp62x")) {
              //如果不存在则创建
              this.db.createObjectStore("ghp62x", { keyPath: "time" }); //创建存储对象（也就是sql中的表）
            }
          };
          // 数据库操作阻塞
          request.onblocked = function (e) {
            console.log("open DB block", e);
          }
        } catch (error) {
          reject(error)
        }

      })

    },
    // 向数据库表中插入数据
    insertDB(item) {
      // 创建一个读写事务
      const transaction = this.db.transaction("ghp62x", "readwrite");
      // 获取对象存储区，返回 IDBObjectStore 对象（也就是sql中的表）
      const objectStore = transaction.objectStore("ghp62x");
      // 向表格写入一条记录
      const request = objectStore.add(item);
      request.onsuccess = function () {
        console.log("write data to DB success");
      };
      request.onerror = function () {
        console.log("write data to data error");
      };
    },
    // 获取数据库中的表
    getDB() {
      // 数据库的操作是一个异步的过程，所以要在回调后执行
      const _this = this;
      return new Promise((reslove, reject) => {
        try {
        // 创建一个只读的事物
        const transaction = this.db.transaction("ghp62x", "readonly");
        // 获取对象存储区，返回 IDBObjectStore 对象的引用（也就是sql中的表）
        const objectStore = transaction.objectStore("ghp62x");
        // 获取表格中的所有数据
        const request = objectStore.getAll();
        request.onsuccess = function (event) {
          console.log("read DB success");
          _this.objStore = event.target.result.reverse();
          reslove();
        };
        request.onerror = function (e) {
          reject(e);
          console.log("read DB error", e);
        };
        } catch (error) {
          reject(error);
        }

      });
    },
    exportFile: debounce(
      function () {
        const stringData = document.querySelector(
          ".ql-container .ql-editor"
        ).innerHTML;
        const blob = new Blob([stringData], {
          type: "text/plain",
        });
        const objectURL = URL.createObjectURL(blob);
        const aTag = document.createElement("a");
        aTag.href = objectURL;
        const prefix = this.custom === "lens" ? "skilscreen" : "cardstock";
        aTag.download = prefix + "_config.txt";
        aTag.click();
        URL.revokeObjectURL(objectURL);
        this.$message.success(this.$t("EXPORT_SUCCESS"));
        const item = {
          time: new Date().getTime(),
          name: this.table.project,
          type: this.custom === "lens" ? "LENS_CUSTOM" : "CARD_CUSTON",
          action: "EXPORT",
          content: new File([blob], aTag.download),
        };
        this.insertDB(item);
      },
      500,
      true
    ),

    importFile(f) {
      const suffix = f.name.split(".").pop();
      if (suffix !== "txt") {
        this.$refs.upload.clearFiles();
        return this.$message.error(this.$t("MUST_TXT"));
      }
      this.readFile(f.raw);

    },
    exceedFile(f, fList) {
      this.$set(fList[0], "raw", f[0]);
      this.$set(fList[0], "name", f[0].name);
      this.readFile(f[0]);
    },
    readFile(raw) {
      const _this = this;
      let reader = new FileReader();
      //读取上传的文件内容转为字符串
      reader.onload = function () {
        let fileTxt = "";
        if (reader.result) fileTxt = reader.result;
        _this.$parent.$refs.editor.setHtml(2, this.locale, fileTxt);
        _this.$message.success(_this.$t("IMPORT_SUCCESS"));
        const item = {
          time: new Date().getTime(),
          name: _this.table.project,
          type: _this.custom === "lens" ? "LENS_CUSTOM" : "CARD_CUSTON",
          action: "IMPORT",
          content: raw,
        }; // file是bold的子类
        _this.insertDB(item);
      };
      reader.readAsText(raw);
    },
    downloadFile(f) {
      const objectURL = URL.createObjectURL(f);
      const aTag = document.createElement("a");
      aTag.href = objectURL;
      aTag.download = f.name;
      aTag.click();
      URL.revokeObjectURL(objectURL);
      this.$message.success(this.$t("FILE_DOWNLOAD_SUCCESS"));
    },
    writeFile(f){
      const _this = this;
        let reader = new FileReader();
        //读取上传的文件内容转为字符串
        reader.onload = function () {
          let fileTxt = "";
          if (reader.result) fileTxt = reader.result;
          _this.$parent.$refs.editor.setHtml(2, "en", fileTxt);
          _this.$message.success(_this.$t("FILE_APPLY_SUCCESS"));
        };
        reader.readAsText(f);
        this.applyConfirmVis = false
    },
    applyFile(f, t) {
      if (
        (t === "LENS_CUSTOM" && this.custom !== "lens") ||
        (t === "CARD_CUSTON" && this.custom === "lens")
      ) {
        this.itemFile = f
        this.applyConfirmVis = true
      } else {
        this.writeFile(f)
      }
    },
  },
};
</script>

<style lang="scss" scoped>
